*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
:root{
    --bg1:rgb(255,255,255,1);
    --shade1:rgb(0,0,0,0.03);
    --blue:rgb(0, 119, 179);
    --labeltext:white;
    --labelbg:black;
    --text:black;
    --line:rgb(0,0,0,0.1);
    --img:0;
}
:root.dark{
    --bg1:rgb(38, 38, 38);
    --shade1:rgb(77, 77, 77);
    --blue:rgb(0, 119, 179);
    --labeltext:black;
    --labelbg:white;
    --text:white;
    --line:rgb(255,255,255,0.25);
    --img:1;
}
.flex{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}
#infin{
    width:100vw;
    height:100vh;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
    background-color:var(--bg1);
    border-top:1px solid var(--line);
}


#infin .main{
    position: relative;
    width:calc(100% - 250px);
    height:100vh;
}
#infin .main .nav{
    height:70px;
    position: relative;
    justify-content: space-between;
    border-bottom:1px solid var(--line);
    padding:0 10px;
}
#infin .main .nav form{
    width:400px;
    height:50px;
    background-color: var(--shade1);
    border-radius:8px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding:0 5px;
}
#infin .main .nav form input{
    background: none;
    outline: none;
    border: none;
    font-size:12px;
    padding-right:10px;
    padding-left:5px;
    width:100%;
    color: var(--text);
}
#infin .main .nav form input::placeholder{
    color:var(--text);
    opacity:0.5;
}
#infin .main .nav form i{
    padding:10px;
    font-size:15px;
    cursor: pointer;
    color:var(--text);
    opacity:0.5;
}
#infin .main .nav .options{
    height:100%;
    position: relative;
    padding:0 10px;
}
#infin .main .nav .options img{
    height:20px;
    cursor: pointer;
    margin:0 10px;
    padding:10px 0px;
    box-sizing: content-box;
    transition:0.3s;
    opacity:0.5;
    filter: invert(var(--img));
}
#infin .main .nav .options i{
    height:20px;
    width:20px;
    text-align: center;
    line-height:20px;
    transition:0.3s;
    opacity:0.5;
    color:var(--text);
    cursor: pointer;
    margin:0 10px;
    padding:10px 0px;
    box-sizing: content-box;
}
#infin .main .nav .options img:hover,#infin .main .nav .options i:hover{
    opacity: 1;
}
#infin .main .nav .options img:nth-child(2){
    height:30px;
}
#infin .main .nav .options.dark img:nth-child(3){
    display: none;
}
#infin .main .nav .options.dark img:nth-child(2){
    display:block;
}
#infin .main .nav .options.light img:nth-child(3){
    display: block;
}
#infin .main .nav .options.light img:nth-child(2){
    display: none;
}

#infin .main .nav .options label{
    position:absolute;
    bottom:0;
    transform:translateY(calc(50% + 0px));
    color: var(--labeltext);
    background-color: var(--labelbg);
    font-size:10px;
    font-weight:bold;
    letter-spacing: 1px;
    opacity:0;
    transition:0.3s;
    padding:5px 10px;
    border-radius:5px;
}

#infin .main .nav .options i:hover ~ label:nth-child(6){
    opacity:1;
}
#infin .main .nav .options img:nth-child(2):hover ~ label:nth-child(7){
    opacity:1;
}
#infin .main .nav .options img:nth-child(3):hover ~ label:nth-child(8){
    opacity:1;
}
#infin .main .nav .options img:nth-child(4):hover ~ label:nth-child(9){
    opacity:1;
}
#infin .main .nav .options img:nth-child(5):hover ~ label:nth-child(10){
    opacity:1;
}



#infin .sidebar{
    width:250px;
    height:100vh;
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    border-right:1px solid var(--line);
}
#infin .sidebar .profile{
    width:100%;
    height:70px;
    position: relative;
    justify-content: flex-start;
    padding:0 10px;
}
#infin .sidebar .profile .logo{
    width:45px;
    height:45px;
    border-radius:5px;
    background-color:rgb(255, 230, 230);
    line-height:45px;
    text-align: center;
    font-size:21px;
    letter-spacing:1px;
    color:rgb(153, 0, 0);
    font-weight:bold;
}
#infin .sidebar .profile .name{
    position: relative;
    width:calc(100% - 45px);
    padding-left:8px;
    top:0;
    left:0;
    transform:none;
    flex-direction: column;
    align-items:flex-start;
}
#infin .sidebar .profile .name h3{
    color: black;
    font-size:13px;
    letter-spacing:1px;
    margin:4px 0;
    color:var(--text);
}
#infin .sidebar .profile .name p{
    font-size:11px;
    color: var(--text);
}
#infin .sidebar .compose{
    height:50px;
    width:calc(100% - 30px);
    background-color: var(--blue);
    color:white;
    border-radius:10px;
    margin-left:15px;
    font-size:14px;
    cursor: pointer;
    padding:10px 20px;
    justify-content: flex-start;
}
#infin .sidebar .compose img{
    width:20px;
    filter:invert(1);
    margin-right:15px;
}
#infin .sidebar .button{
    height:50px;
    padding:10px 20px;
    color:var(--text);
    margin:10px 15px 10px 15px;
    width:calc(100% - 30px);
    cursor: pointer;
    justify-content: flex-start;
    font-size:13px;
}
#infin .sidebar .button img{
    width:20px;
    margin-right:15px;
    filter: invert(var(--img));
}


#infin .main .mails{
    width:420px;
    border-right:1px solid var(--line);
    height:calc(100% - 70px);
    position: relative;
}
#infin .main .sort{
    position: relative;
    height:50px;
    width:100%;
    border-bottom:1px solid var(--line);
    justify-content:space-between;
}
#infin .main .sort span,#infin .main .sort label{
    margin:0 10px;
    font-size:13px;
    cursor: pointer;
    padding: 10px;
    transition:0.3s;
    color:var(--text);
}
#infin .main .sort span.select{
    background-color: var(--shade1);
}
#infin .main .sort.all div span:nth-child(1),
#infin .main .sort.read div span:nth-child(2)
,#infin .main .sort.unread div span:nth-child(3){
    color:var(--blue);
}
#infin .main .sort div .line{
    position: absolute;
    bottom:-2px;
    left:0;
    height:4px;
    width:50px;
    left:0px;
    background-color: var(--blue);
    border-radius:4px;
    transition:0.3s;
}
#infin .main .sort.all div .line{
    left:5px;
}
#infin .main .sort.read div .line{
    left:66px;
}
#infin .main .sort.unread div .line{
    left:146px;
}